import React from "react";
import "./styles/Service.css";
import Egg from "../images/desktop/image-transform.jpg";
import Standout from "../images/desktop/image-stand-out.jpg";
import GD from "../images/desktop/image-graphic-design.jpg";
import Photography from "../images/desktop/image-photography.jpg";

export default function Service() {
  return (
    <section className="services">
      <div className="service">
        <div className="content">
          <h2 className="content-title">Transform your brand.</h2>
          <p>
            We are a full stack service creative agency specializing in helping
            brands grow fast. Engage your clients through compelling visuals
            that do most of the marketing of you.
          </p>
          <button className="btn-underlined">Learn more</button>
        </div>
        <div className="img">
          <img src={Egg} alt="egg" />
        </div>
      </div>
      <div className="service">
        <div className="content">
          <h2 className="content-title" style={{ width: "100%" }}>
            Standout to the right audience.
          </h2>
          <p>
            We are a full stack service creative agency specializing in helping
            brands grow fast. Engage your clients through compelling visuals
            that do most of the marketing of you.
          </p>
          <button className="btn-underlined">Learn more</button>
        </div>
        <div className="img">
          <img src={Standout} alt="stand out" />
        </div>
      </div>
      {/* <div className="service">
        <div
          className="content"
          style={{
            backgroundImage: `url(${GD})`,
            backgroundPosition: "center",
            backgroundSize: "cover"
          }}
        >
          <h2>Grapic Design</h2>
          <p>
            Great design makes you memorable. We deliver artwork that
            underscores your brand message and captures potential clients
            attention.
          </p>
        </div>
      </div> */}
    </section>
  );
}
